import { createItems, getSizeEditor } from '../utils';

export default {
  id: ['Select', 'i-select'],
  name: '',
  eventTypes: [],
  type: ['input', 'select'],
  props: {
    value: {
      label: '',
      editors: 'text',
    },
    multiple: {
      label: '',
      editors: 'boolean',
    },
    filterable: {
      label: '',
      editors: 'boolean',
    },
    size: {
      label: '',
      editors: getSizeEditor(),
    },
    placeholder: {
      label: '	',
      editors: 'text',
    },
    clearable: {
      label: '',
      editors: 'boolean',
    },
    disabled: {
      label: '',
      editors: 'boolean',
    },
    readonly: {
      label: '',
      editors: 'boolean',
    },
    prefix: {
      label: '',
      editors: 'icon',
    },
  },

  optionEditor: {
    tag: 'i-option',
    getData(node) {
      return {
        value: node.getAttr('value'),
        label: node.getAttr('label') || node.getText(),
      };
    },
    setData(node, data) {
      node.setAttr('value', data.value);
      if (node.hasAttr('label')) {
        node.setAttr('label', data.label);
      }else{
        node.replaceText(data.label)
      }
    },
  },

};
