import iview from 'view-design';

iview.Modal.mixins = iview.Modal.mixins || [];


iview.Modal.mixins.push({
  mounted(){
    this.resizeMask();
  },
  updated(){
    this.resizeMask();
  },
  methods: {
    resizeMask(){

      if (this.$el.dataset.astId) {
        let $mask = this.$el.querySelector('.ivu-modal-mask');
        let $wrap = this.$el.querySelector('.ivu-modal-wrap');

        let rect = document.querySelector('.vuc-ceditor').getBoundingClientRect();

        [$mask, $wrap].forEach($el => {
          if ($el) {
            Object.assign($el.style, {
              top: rect.top + 'px',
              left: rect.left + 'px',
              height: rect.height + 'px',
              width: rect.width + 'px',
              right: 'none',
              bottom: 'none',
              zIndex: 100,
            });
          }
        });
      }

    },
  },
});

export default {
  id: ['Modal'],
  name: '',
  eventTypes: [],
  type: ['container'],
  slots: [{
    slot: 'default',
    name: '',
    selector: '.ivu-modal-body',
  }, {
    slot: 'header',
    name: '',
    selector: '.ivu-modal-header',
  }, {
    slot: 'footer',
    name: '',
    selector: '.ivu-modal-footer',
  }, {
    slot: 'close',
    name: '',
    selector: '.ivu-modal-close',
  }],
  props: {
    value: {
      label: '',
      editors: 'boolean',
    },
    title: {
      label: '',
      editors: 'text',
    },
    width: {
      label: '',
      editors: 'text',
    },
    draggable: {
      label: '',
      editors: 'boolean',
    },
    okText: {
      label: '',
      editors: 'text',
    },
    cancelText: {
      label: '',
      editors: 'text',
    },
    footerHide: {
      label: '',
      editors: 'boolean',
    },
  },
};
